header {
    box-shadow: 0 0 6px 0;
    
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 39px 6px 39px;
}

#Header__logo__image {
    height: 72px;
    cursor: pointer;
}

.Nav__profile {
    all: unset;
    border: 1px solid var(--color-darkWhite);
    border-radius: var(--border-radius);
    padding: 8px;
    background-color: var(--color-1-c);
    color: var(--color-white);
    cursor: pointer;
}